.myproject {
    color: white;
    position: absolute;
    margin-top: 75px;
    animation-duration: 2s;
    animation-name: myprojectblockkf;
    display: grid;
    grid-template-columns: 1fr 10fr 3fr;
    width: 700px;
    margin-left: 320px;
}

.tablenumber0 {
    border: 1px solid white;
    height: 40px auto;
    text-align: center;
    color: orange;
}

.tablemyinfo {
    border: 1px solid white;
    height: 40px auto;
    text-align: center;
    color: #00ffd6;
    font-weight: 600;
}

.tablea {
    border: 1px solid white;
    text-align: center;
    height: 40px auto;
    color: greenyellow;
}

.tablea a {
    text-decoration: none;
    color: greenyellow;
}

@keyframes myprojectblockkf {
    0%{
        opacity: 0;
    }
    100%{
        opacity: 1;
    }
}