.myinfoblock {
    color: white;
    position: absolute;
    margin-top: 75px;
    animation-duration: 2s;
    animation-name: myinfoblockkf;
}

.myinfoblock p {
    text-align: center;
    font-size: 20px;
    margin-top: 150px;
}

.myinfoblock b {
    color: orange;
}

@keyframes myinfoblockkf {
    0%{
        opacity: 0;
    }
    100%{
        opacity: 1;
    }
}