.header {
    position: fixed;
    color: white;
    z-index: 999;
}

.glavnaya {
    position: absolute;
    text-decoration: none;
    margin-top: 20px;
    width: 120px;
    height: 30px;
    text-align: center;
    font-size: 19px;
    margin-left: 600px;
    color: white;
    font-family: Script MT Bold;
    animation-name: glavnayakf;
    animation-duration: 1.5s;
}
.glavnaya:hover {
    transition: 1s all;
    border-bottom: 1px solid blue;
}
a.aact .glavnaya {
    transition: .5s all;
    border-bottom: 1px solid blue;
}
a.aact .myinfo {
    transition: .5s all;
    border-bottom: 1px solid blue;
}
a.aact .myprojects {
    transition: .5s all;
    border-bottom: 1px solid blue;
}

.myinfo {
    position: absolute;
    margin-top: 20px;
    width: 120px;
    height: 30px;
    text-align: center;
    font-size: 19px;
    margin-left: 480px;
    color: white;
    font-family: Script MT Bold;
    animation-name: myinfokf;
    animation-duration: 2s;
}
.myinfo:hover {
    transition: 1s all;
    border-bottom: 1px solid blue;
}

.myprojects {
    position: absolute;
    margin-top: 20px;
    width: 120px;
    height: 30px;
    text-align: center;
    font-size: 19px;
    margin-left: 720px;
    color: white;
    font-family: Script MT Bold;
    animation-name: myprojectkf;
    animation-duration: 1s;
}
.myprojects:hover {
    transition: 1s all;
    border-bottom: 1px solid blue;
}

@keyframes glavnayakf {
    0%{
        margin-top: -500px;
    }
    100%{
        margin-top: 20px;
    }
}

@keyframes myinfokf {
    0%{
        margin-left: -800px;
    }
    100%{
        margin-left: 480px;
    }
}

@keyframes myprojectkf {
    0%{
        margin-left: -1000px;
    }
    100%{
        margin-left: 720px;
    }
}

@media screen and (max-width: 500px) {
    .myinfo {
        margin-left: 125px;
        width: 60px;
        height: 20px;
        font-size: 13px;
        animation-name: myinfokfph;
        animation-duration: 2s;
    }
    @keyframes myinfokfph {
        0%{
            margin-left: -5000px;
        }
        100%{
            margin-left: 125px;
        }
    }
}

@media screen and (max-width: 500px) {
    .glavnaya {
        margin-left: 185px;
        width: 60px;
        height: 20px;
        font-size: 13px;
        animation-name: glavnayakfph;
        animation-duration: 1.5s;
    }
    @keyframes glavnayakfph {
        0%{
            margin-top: -500px;
        }
        100%{
            margin-top: 20px;
        }
    }
}

@media screen and (max-width: 500px) {
    .myprojects {
        margin-left: 245px;
        width: 60px;
        height: 20px;
        font-size: 13px;
        animation-name: myprojectkfph;
        animation-duration: 1s;
    }
    @keyframes myprojectkfph {
        0%{
            margin-left: -500px;
        }
        100%{
            margin-left: 245px;
        }
    }
}